import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/home/index.vue')
  },
  {
    path: '/contactUs',
    name: 'contactUs',
    component: () => import('@/views/contactUs/index.vue'),
    meta: {
      title: '联系我们'
    }
  },
  {
    path: '/system',
    name: 'system',
    component: () => import('@/views/system/index.vue'),
    meta: {
      title: '货代服务系统'
    }
  },
  {
    path: '/product',
    name: 'product',
    component: () => import('@/views/imports/index.vue'),
    meta: {
      title: '产品与服务'
    }
  },
  {
    path: '/afterSales',
    name: 'afterSales',
    component: () => import('@/views/afterSales/index.vue'),
    meta: {
      title: '在线售后'
    }
  },
  {
    path: '/bulkcargo',
    name: 'bulkcargo',
    component: () => import('@/views/bulkcargo/index.vue'),
    meta: {
      title: '散货船货运输'
    }
  },
  {
    path: '/global',
    name: 'global',
    component: () => import('../views/global/index.vue'),
    meta: {
      title: '国际物流服务'
    }
  },
  {
    path: '/airplant',
    name: 'airplant',
    component: () => import('@/views/global/componets/airplant.vue'),
    meta: {
      title: '国际航空货代'
    }
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('@/views/about/index.vue'),
    meta: {
      title: '关于岭南泵业'
    }
  },
  {
    path: '/list',
    name: 'list',
    component: () => import('@/views/news1/index.vue'),
    meta: {
      title: '产品列表'
    },
    children: [
      {
        path: '',
        name: 'list',
        component: () => import('@/views/news1/list.vue')
      },
      {
        path: 'detail',
        name: 'detail',
        component: () => import('@/views/news1/detail.vue'),
        meta: {
          title: '产品详情'
        }
      }
    ]
  },
  {
    path: '/news2',
    name: 'news2',
    component: () => import('@/views/news2/index.vue'),
    meta: {
      title: '新闻2'
    }
  }
];

const router = new VueRouter({
  // mode: 'history',
  base: '/',
  routes
});

export default router;
