import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    mainBannerList: [
      'static/img/main-banner/default.jpg',
      'static/img/main-banner/default.jpg',
      'static/img/main-banner/default.jpg'
    ],
    subBannerList: [
      'static/img/sub-banner/default.jpg',
      'static/img/sub-banner/default.jpg',
      'static/img/sub-banner/default.jpg'
    ],
    otherImgMap: {
      about_top: 'static/img/other/default_about_top.jpg',
      about_img1: 'static/img/other/default_about_img1.jpg',
      about_img2: 'static/img/other/default_about_img2.jpg',
      product_top: 'static/img/other/default_product_top.jpg',
      aftersafes_top: 'static/img/other/default_aftersafes_top.jpg',
      contact_top: 'static/img/other/default_contact_top.jpg',
      contact_img1: 'static/img/other/default_contact_img1.jpg'
    }
  },
  getters: {
  },
  mutations: {
    async initWebMeta (state) {
      try {
        const res = await this.$http.get('/api/getWebMeta', {})
        if (res.data.main_banner_lst && res.data.main_banner_lst.length > 0) {
          state.mainBannerList = res.data.main_banner_lst
        }
        if (res.data.sub_banner_lst && res.data.sub_banner_lst.length > 0) {
          state.subBannerList = res.data.main_banner_lst
        }
        if (res.data.other_lst) {
          res.data.other_lst.forEach(item => {
            if (state.otherImgMap[item.key] && item.src) {
              state.otherImgMap[item.key] = item.src
            }
          })
        }
        console.log('===========================');
        console.log('initWebMeta 成功');
        console.log(state);
        console.log('===========================');
      } catch (err) {
        console.log('===========================');
        console.log('initWebMeta 失败');
        console.log(err);
        console.log('===========================');
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
